    text-indent: -9999px;
    overflow: hidden;
    vertical-align: middle; }
  #menu_toggle span {
    position: relative;
    margin-top: 8px;
    margin-bottom: 8px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
    #menu_toggle span, #menu_toggle span:before, #menu_toggle span:after {
      display: block;
      width: 100%;
      height: 3px;
      background-color: #4987a4;
      transition-duration: 0.3s;
      /* autoprefixer: off */
      -webkit-transition-property: background-color, -webkit-transform;
      -moz-transition-property: background-color, -moz-transform;
      -o-transition-property: background-color, -o-transform;
      transition-property: background-color, transform; }
    #menu_toggle span:before, #menu_toggle span:after {
      position: absolute;
      content: ""; }
    #menu_toggle span:before {
      top: -8px; }
    #menu_toggle span:after {
      top: 8px; }
  #menu_toggle.is-active span {
    background-color: transparent; }
  #menu_toggle.is-active span:before {
    transform: translateY(8px) rotate(45deg); }
  #menu_toggle.is-active span:after {
    transform: translateY(-8px) rotate(-45deg); }

